import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { ComService } from 'src/app/core/com.service';
import { Md5 } from 'ts-md5'

@Component({
  selector: 'app-auth',
  templateUrl: './auth.component.html',
  styleUrls: ['./auth.component.css']
})
export class AuthComponent implements OnInit {

  constructor(
    private com :ComService,
    private route:ActivatedRoute,
  ) { }
  form :any ={}
  routeName :string = 'login'
  ngOnInit(): void {
    this.route.params.subscribe(p=>{
      this.routeName = p.name;
    })
  }
  login(){
    let {key,...obj} = this.form
     
    obj.key = Md5.hashStr(key);
    console.log(obj)
    this.com.login(obj)
  }
  register(){
    let {password,...obj} = this.form     
    obj.password = Md5.hashStr(password);
    console.log(obj)
    delete obj.confirm_password;
    this.com.register(obj)
  }
}
